import Box from '@mui/material/Box';
import { CheckoutForm, CheckOutTotal } from '../RouteComponents/checkoutComponents';
import '../Styles/checkout.css';

const Checkout = () => {
    return (<>
        <Box component='div' className='checkoutPart'>
            <CheckOutTotal />
            <CheckoutForm />
        </Box>
    </>)
}

export default Checkout;