import { Box } from '@mui/system';
import { Link } from 'react-router-dom';
import {
    Button, SwipeableDrawer,
    Typography, Badge, IconButton, List, ListItem,
} from '@mui/material';
import {
    Search, ShoppingCart, FavoriteBorder,
    Facebook, Instagram, Pinterest, CloseOutlined, Cancel,
    RemoveCircle, AddCircle, Close, Menu, Star
} from '@mui/icons-material';
import { useState, useEffect, useContext } from 'react';
import { CartContext } from '../context';
import { useHistory } from 'react-router';

const toggleSearchBarVisibility = () => {
    if (document.querySelector('.searchBar').hasAttribute('id')) {
        document.querySelector('.searchBar').removeAttribute('id')
    }
    else {
        document.querySelector('.searchBar').setAttribute('id', 'navVisible')
    }
}
const toggleMobileNav = () => {
    document.querySelector('.navLinks').classList.toggle('navLinksVisible')
}


export const AppNavBar = () => {

    const [navDrawer, setNavDrawer] = useState({ anchor: 'right', state: false });
    const context = useContext(CartContext);
    const [cart, setCart] = useState(context.cart);
    const [history, setH] = useState(useHistory().location.pathname);
    useEffect(() => {
        setCart(context.cart);
    }, [context]);

    const getActiveLink = (link) => {
        return link == history ? 'activeLink' : '';
    }

    const toggleDrawer = (anchor, openOrClose) => (event) => {
        if (
            event &&
            event.type === 'keydown' &&
            (event.key === 'Tab' || event.key === 'Shift')
        ) {
            return;
        }
        setNavDrawer({ anchor: anchor, state: openOrClose });
    };

    return (<Box component="nav" className="nav">
        <Box className="navLogo">
            <Link to="/">
                AnimeStore
            </Link>
        </Box>
        <Box sx={{ ml: 10 }} className="navLinks">
            <Link to="/" className={getActiveLink('/')}
                onClick={() => setH('/')}>Accueil</Link>
            <Link to="/Store" className={getActiveLink('/Store')}
                onClick={() => setH('/Store')}>Store</Link>
            <Link to="/Cart" className={getActiveLink('/Blog')}
                onClick={() => setH('/Cart')}>Pannier</Link>
            <Link to="/About" className={getActiveLink('/About')}
                onClick={() => setH('/About')}>A propos</Link>
            <Link to="/Contact" className={getActiveLink('/Contact')}
                onClick={() => setH('/Contact')}>Contact</Link>
        </Box>

        <Box component="div" className="navMobileHambuger">
            <IconButton><Menu onClick={() => { toggleMobileNav(); }} /></IconButton >
        </Box>

        <Box className="appNavIconPart" >
            <Link to="/about">
                <IconButton onClick={() => { toggleSearchBarVisibility() }}><Search /></IconButton >
            </Link>
            <IconButton onClick={toggleDrawer("right", true)}>
                {
                    cart.intoCart.length > 0 ?
                        <Badge badgeContent={cart.intoCart.length} color="primary">
                            <ShoppingCart color="action" />
                        </Badge> :
                        <ShoppingCart />
                }
            </IconButton >
            <Link to="/users">
                <IconButton><FavoriteBorder /></IconButton >
            </Link>
        </Box>
        <SwipeableDrawer className="navSideBar"
            variant="temporary"
            open={navDrawer.state}
            onClose={toggleDrawer("right", false)}
            anchor="right"
            sx={{ zIndex: 90000, }}
        >
            <Box component="div">
                <Box component="section" className="sbCloser">
                    <h4>Votre pannier</h4>
                    <IconButton onClick={toggleDrawer("right", false)}>
                        <CloseOutlined />
                    </IconButton>
                </Box>
                <List>
                    {
                        cart.intoCart.length ? cart.intoCart.map(itc => <ListItem key={itc.id}>
                            <Box component="div">
                                <img src={process.env.PUBLIC_URL + '/images/' + itc.category + '/' + itc.img} />
                            </Box>
                            <Box>
                                <p>
                                    <span>€ {itc.price} * {itc.quantity} </span>
                                    <br />
                                    {itc.title}
                                </p>
                                <Box>
                                    <IconButton onClick={() => cart.updateIntoCart(itc.id, false)}><RemoveCircle /></IconButton>
                                    <IconButton onClick={() => cart.updateIntoCart(itc.id, true)}><AddCircle /></IconButton>
                                    <IconButton onClick={() => cart.deleteToCart(itc.id)} ><Cancel /></IconButton>
                                </Box>
                            </Box>
                        </ListItem>) : <p>Aucun article dans le panier</p>
                    }
                </List>

            </Box>
            <Box component="section" className="nsBottom">
                <h2>Total</h2>
                <div >
                    <p>Produit : <span>{cart.getTotal('q', cart.intoCart)}</span> </p>
                    <span> {cart.getTotal('price', cart.intoCart)} €</span>
                </div>
                <Link to="/Cart">Voir le pannier</Link>
            </Box>

        </SwipeableDrawer>
        <SearchBar />
    </Box >);
};


export const Banner = ({ type }) => {
    return (<Box component="div" className="pageBanner">
        <img alt="Banniere de contact" style={{ width: "100%", height: '40vh' }}
            src={process.env.PUBLIC_URL + '/images/banners/contactBanner.jpg'} />
        <h1 variant="h1" >{type} </h1>
    </Box>)
}
const SearchBar = () => {
    return (<div className="searchBar">
        <form>
            <input type="search" />
            <IconButton><Search /></IconButton>
        </form>
        <IconButton onClick={() => { toggleSearchBarVisibility() }}><Close /></IconButton>
    </div>)
}




export const ProductModal = ({ productPreview }) => {

    const context = useContext(CartContext);
    const isOnCart = context.cart.isOnCart;
    const modalToggler = context.modalToggler;
    const updateIntoCart = context.cart.updateIntoCart;
    // pmVisible
    return (<Box component="div" className="modal productModal ">
        <Box component="div" className="productPreview">
            <Box component="div" className="ppClose">
                <IconButton onClick={() => { modalToggler('.productModal', 'pmVisible') }}><Close /></IconButton>
            </Box>
            <Box component="div" className="ppImg">
                <img src={process.env.PUBLIC_URL + '/images/' + productPreview.category + '/' + productPreview.image} />
            </Box>
            <Box component="div" className="ppText">
                <h2>{productPreview.title}</h2>
                <p><Star /> <Star /><Star /><Star /><Star /></p>
                <h2>{productPreview.price}</h2>
                <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod
                    tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam,
                    quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo
                    consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse
                    cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non
                    proident, sunt in culpa qui officia deserunt rum.</p>
                <section className="ppCategory"><h3>Category :</h3>  <span>{productPreview.category}</span></section>
                {
                    isOnCart(productPreview.id) != false ?
                        <section className="ppUpToCart">
                            <IconButton onClick={() => updateIntoCart(productPreview.id, false)}><RemoveCircle /></IconButton>
                            <span> {isOnCart(productPreview.id)}</span>
                            <IconButton onClick={() => updateIntoCart(productPreview.id, true)}><AddCircle /></IconButton>
                        </section>
                        : <section className="ppAddToCart">
                            <button>Ajouter au panier</button>
                        </section>
                }
                <section><h3>Partager le produit sur :</h3> <Facebook /> <Instagram /> <Pinterest /></section>

            </Box>
        </Box>
    </Box>);
}



