import Box from '@mui/material/Box';
import { Button, IconButton, Input } from '@mui/material';
import { useContext } from 'react';
import { AddCircle, DeleteOutline, RemoveCircle } from '@mui/icons-material';
import "../Styles/cart.css";
import { Link } from 'react-router-dom';
import { CartContext } from '../context';

export const CartList = () => {
    const context = useContext(CartContext)
    const intoCart = context.cart.intoCart;

    return (<Box component="div" className="cpList">
        <h2>Produit</h2>
        <h2>Prix unitaire</h2>
        <h2>Quantité</h2>
        <h2>Prix total</h2>
        {intoCart.length > 0 ?
            intoCart.map(item =>
                <Box className="cplRow">
                    <Box component="section" className="cplProduct">
                        <img src={process.env.PUBLIC_URL + '/images/' + item.category + '/' + item.img} />
                        <p>{item.title}</p>
                    </Box>
                    <Box component="section" className="cplUnitPrice">
                        <p>{item.price} € </p>
                    </Box>
                    <Box component="section" className="cplQuantity">
                        <p>
                            <IconButton onClick={() => context.cart.updateIntoCart(item.id, false)} >
                                <RemoveCircle />
                            </IconButton>
                            <span>{item.quantity}</span>
                            <IconButton onClick={() => context.cart.updateIntoCart(item.id, true)}>
                                <AddCircle />
                            </IconButton>
                        </p>
                        <IconButton className="cplDelete" onClick={() => context.cart.deleteToCart(item.id)}>
                            <DeleteOutline />
                        </IconButton>
                    </Box>
                    <Box component="section" className="cplTotaPrice">
                        <p>{(item.price * item.quantity)} €</p>
                    </Box>
                </Box>)
            :
            <div className="emptyCart">Pas de produit daans le pannier</div>
        }
    </Box>)
}

export const CartTotal = () => {

    const context = useContext(CartContext)

    return (<Box component="div" className="cpTotal">

        <div className="cptHeader">
            <Link>Continuez le shopping</Link>
            <h1>Total du Pannier</h1>
        </div>

        <form className="cptCoupon" >
            <Input type="text" placeholder="Numero de coupon" />
            <Button>Soumettre</Button>
        </form>

        <Box component="div" className="cptSum">
            <section>
                <h4>Total Hors taxe(HT) </h4>
                <p>{context.cart.getTotal('price', context.cart.intoCart)} €</p>
            </section>
            <section >
                <h4>Total TVA</h4>
                <p>0 €</p>
            </section>
            <hr />
            <section >
                <b>Total</b>
                <p>{context.cart.getTotal('price', context.cart.intoCart)} €</p>
            </section>
            <section >
                {
                    context.cart.getTotal('price', context.cart.intoCart) != 0 ?
                        <Link to="/Checkout">Proceder au paiement</Link>
                        :
                        <button disabled>Proceder au paiement</button>
                }
            </section>
        </Box>
    </Box>)
}