import { CheckCircle } from "@mui/icons-material";
import { Link, TextField } from "@mui/material"
import { useState, useEffect, useContext } from "react"
import { isValidLenght, isValidChar } from "../Tools/formValidator";
import { CartContext } from "../context";

export const CheckoutForm = () => {
    const context = useContext(CartContext);
    const modalToggler = context.modalToggler;

    const [errors, setErrors] = useState([]);
    const [formField, setFormField] = useState({
        'nom': '', 'prenom': '', 'adresse': '', 'pays': '',
        'code-postal': '', 'identité-carte': '', 'numero-carte': ''
    });
    const [formValid, setFormValid] = useState(false);
    const [villes, setVilles] = useState([]);
    const pays = [
        { nom: 'Togo', citys: ['Lomé', 'Atakapmey'] },
        { nom: 'Benin', citys: ['Cotonou', 'Godomey'] },
        { nom: 'France', citys: ['Paris', 'Marseille'] },
    ]

    const checkFormState = (formField, errors) => {

        if (formField.nom != '' && formField.prenom != ''
            && formField.adresse != '' && formField.pays != ''
            && formField['code-postal'] != '' && formField['identité-carte'] != ''
            && formField['numero-carte'] != '') {
            return (errors.length == 0) ? true : false;
        }
        else {
            return false;
        }

    }

    useEffect(() => setFormValid(checkFormState(formField, errors)), [formField])
    const handleFieldChange = (event) => {
        let key = event.target.name;
        if (['nom', 'prenom', 'adresse', 'identité-carte'].includes(key)) {
            isValidLenght(errors, setErrors, key, event.target.value, 5, 15);
            if (['nom', 'prenom'].includes(key)) {
                isValidChar('isAlpha', errors, setErrors, key, event.target.value, 'Le nom ou le prenom')
            }
            else {
                isValidChar('isAlphaWithSpace', errors, setErrors, key, event.target.value, 'Le nom ou le prenom de la carte')
            }
        }
        if (key == "numero-carte") {
            isValidLenght(errors, setErrors, key, event.target.value, 8, 10);
        }
        if (key == 'pays') {
            handleCountryChange(event.target.value)
        }
        if (key == 'code-postal') {
            isValidChar('isAlphaNumeric', errors, setErrors, key, event.target.value, 'Le code-postal')
        }
        setFormField({ ...formField, [key]: event.target.value })

    }

    const handleCountryChange = (country) => {
        for (let i = 0; i < pays.length; i++) {
            if (pays[i].nom == country) {
                return setVilles(pays[i].citys);
            }
        }
    }

    const getError = (name) => {
        const errorList = errors.filter(item => item.name == name);
        if (errorList.length > 0) {
            return errorList.map(err => <span className="errorField" key={err.type}>{err.error}</span>)

        }
    }
    const formBtn = () => {
        if (formValid == false) {
            return <button disabled>Soumettre </button>
        } else {
            return <button className="coBtnEnable"
                onClick={() => modalToggler('.checkoutStatusModal', 'ckmVisible')}>
                Soumettre </button>
        }
    };

    return <><form className="coForm">
        <div className="formSection">
            <label>Nom</label>
            <TextField fullWidth name="nom"
                value={formField.nom} onChange={(event) => handleFieldChange(event)} />
            {getError('nom')}
        </div>

        <div className="formSection">
            <label>Prenom</label>
            <TextField fullWidth name="prenom"
                value={formField.prenom} onChange={(event) => handleFieldChange(event)} />
            {getError('prenom')}
        </div>

        <div className="formSection">
            <label>Adresse</label>
            <TextField fullWidth name="adresse"
                value={formField.adresse} onChange={(event) => handleFieldChange(event)} />
            {getError('adresse')}
        </div>

        <div className="formSection">
            <label>Pays</label>
            <select onChange={(event) => { handleFieldChange(event) }} name="pays">
                <option selected disabled>Pays...</option>
                {
                    pays.map(pay => <option key={pay.nom}>{pay.nom}</option>)
                }
            </select>
        </div>

        <div className="formSection">
            <label>Ville</label>
            <select
                name="villes">
                {
                    villes.map(ville => <option key={ville} >{ville}</option>)
                }
            </select>
        </div>

        <div className="formSection">
            <label>Code postal</label>
            <TextField fullWidth id="fullWidth"
                name="code-postal" onChange={(event) => { handleFieldChange(event) }} />
            {getError('code-postal')}
        </div>


        <h3>Detail de carte de paiement</h3>

        <div className="formSection">
            <label>Nom et prenom sur la carte</label>
            <TextField fullWidth id="fullWidth" type="text"
                name="identité-carte" onChange={(event) => { handleFieldChange(event) }} />
            {getError('identité-carte')}
        </div>

        <div className="formSection">
            <label>Numero de carte</label>
            <TextField fullWidth id="fullWidth" type="number"
                name="numero-carte" onChange={(event) => { handleFieldChange(event) }} />
            {getError('numero-carte')}
        </div>
        <div className="formBtn">
            {formBtn()}
        </div>
    </form>
        <div className="modal checkoutStatusModal">
            <div className="checkoutStatus">
                <CheckCircle />
                <p>Votre commande a bien été enregistrer ,<br />
                    votre livraison sera faite dans les delais,Merci de votre confiance</p>
                <Link to="/">Continuez shopping</Link>
                <button onClick={() => modalToggler('.checkoutStatusModal', 'ckmVisible')} >Fermer</button>
            </div>
        </div>
    </>
}

export const CheckOutTotal = () => {
    return <div className="coTotal">
        <h1>Total:</h1>
        <p>1200 €</p>
    </div>
}