import * as React from 'react';
import Box from '@mui/material/Box';
import {
    Button,
    Typography, TextField
} from '@mui/material';
import { Search } from '@mui/icons-material';

import '../Styles/contact.css';

export const ContatForm = () => {
    return (
        <Box component="div" className='contactForm'>
            <Box component='div'>
                <Typography variant="h4">
                    Envoyez nous un message
                </Typography>
                <Box component='form'>
                    <TextField type="text" />
                    <TextField type="text" />
                    <Button className="semiRounded">Valider</Button>
                </Box>
            </Box>
            <Box component='div' className='contactInfos'>
                <Box component='section'>
                    <Search />
                    <h3>
                        Adresse Physique<br />
                        <p>Arigato Store Center 8th floor, 379 Hudson St, New York, NY 10018 US </p>
                    </h3>
                </Box>
                <Box component='section'>
                    <Search />
                    <h3>
                        Numero<br />
                        <p>
                            <a href="#">+336 786423689</a>
                        </p>
                    </h3>
                </Box>
                <Box component='section'>
                    <Search />
                    <h3>
                        Adresse electronique<br />
                        <p>
                            <a href="#">monEcommerce06@gmail.com</a>
                        </p>
                    </h3>
                </Box>
            </Box>
        </Box>
    )
}