import data from './data';
import { DataContext, CartContext } from './context';
import { useEffect, useState } from 'react';
import '@fontsource/poppins';
import './Styles/app.css';
import { AppNavBar, Footer } from './Components/global';
import Accueil from './Routes/Accueil';
import Store from './Routes/Store';
import About from './Routes/About';
import Cart from './Routes/Cart';
import Contact from './Routes/Contact';
import Checkout from './Routes/Checkout';
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import './Components/additinal';
import { ProductModal } from './Components/global';
import './Styles/responsive.css';


function App() {
  const [intoCart, setIntoCart] = useState([]);
  useEffect(() => {
    if (localStorage.getItem('tempCart')) {
      setIntoCart(JSON.parse(localStorage.getItem('tempCart')))
    }
  }, []);

  const addToCart = (added) => {
    let copyTable = intoCart.slice();
    const newCartItem = {
      id: added.id, title: added.title, quantity: 1,
      img: added.image, category: added.category, price: added.price
    };

    if (copyTable.filter((ct,) => ct.id === added.id).length <= 0) {
      copyTable.push(newCartItem)
      if (intoCart.length == 0) {
        localStorage.setItem('tempCart', JSON.stringify([newCartItem]))
      }
      else {
        intoCart.push(newCartItem)
        localStorage.setItem('tempCart', JSON.stringify(intoCart));
      }
    }
    setIntoCart(copyTable);
  }

  const updateIntoCart = (updatedId, typeUpdate) => {
    const copyTable = intoCart.slice();
    for (let index = 0; index < intoCart.length; index++) {
      if (intoCart[index].id == updatedId) {
        if (typeUpdate == true) {
          copyTable[index].quantity++;
        }
        else {
          copyTable[index].quantity > 1 ? copyTable[index].quantity-- : copyTable[index].quantity = copyTable[index].quantity;
        }
        localStorage.setItem('tempCart', JSON.stringify(copyTable))
        return setIntoCart(copyTable)
      }
    }
  }

  const deleteToCart = (deletedId) => {
    const copyTable = intoCart.slice();
    for (let index = 0; index < intoCart.length; index++) {
      if (intoCart[index].id == deletedId) {
        copyTable.splice(index, 1);
        localStorage.setItem('tempCart', JSON.stringify(copyTable))
        return setIntoCart(copyTable);
      }
    }
  }

  const getTotal = (type, array) => {
    if (type == 'price') {
      let t = 0;
      for (let index = 0; index < array.length; index++) {
        t = t + (array[index].quantity * array[index].price)
      }
      return Math.round(t);
    }
    else {
      return array.length
    }
  }
  const isOnCart = (itemId) => {
    for (let index = 0; index < intoCart.length; index++) {
      if (intoCart[index].id == itemId) {
        return intoCart[index].quantity;
      }
    }
    return false;
  }

  const [productPreview, setPP] = useState({ title: 'Bannane', price: '5 €', image: "ok.jpg", category: 'ok' });

  const cart = { intoCart, addToCart, updateIntoCart, deleteToCart, isOnCart, getTotal };

  const modalToggler = (container, containerVisibleClass) => {
    document.querySelector(container).classList.remove(containerVisibleClass)
  }
  return (
    <Router>
      <CartContext.Provider value={{ cart, modalToggler }}>
        <AppNavBar />
        <ProductModal productPreview={productPreview} />
      </CartContext.Provider>
      <Switch>
        <Route path="/About">
          <About />
        </Route>

        <Route path="/Store">
          <DataContext.Provider value={{ intoCart, addToCart, data, setPP }}>
            <Store />
          </DataContext.Provider>
        </Route>

        <Route path="/Cart">
          <CartContext.Provider value={{ cart }}>
            <Cart />
          </CartContext.Provider>
        </Route>

        <Route path="/Contact">
          <Contact />
        </Route>

        <Route path="/Checkout">
          <CartContext.Provider value={{ modalToggler }}>
            <Checkout />
          </CartContext.Provider>
        </Route>

        <Route path="/">
          <DataContext.Provider value={{ intoCart, addToCart, data, setPP }}>
            <Accueil />
          </DataContext.Provider>
        </Route>

      </Switch>
      <Footer />
    </Router>
  );
}
export default App;
