import * as React from 'react';
import Box from '@mui/material/Box';
import {
    Button,
    Typography, Tabs, Tab, Card, CardActionArea,
    CardActions
} from '@mui/material';
import { AddShoppingCart, FavoriteBorderOutlined, } from '@mui/icons-material';
import { useState } from 'react';
import CardContent from '@mui/material/CardContent';
import { useLocation } from 'react-router';
import { Carousel } from 'react-responsive-carousel';
import '../Styles/store.css';

const toggleClass = (target) => {
    document.querySelector('.' + target).classList.toggle(target + 'Visible')
}

export const BannerSlide = () => {
    var carouselItems = [
        {
            name: "Anime Character Collection 2022",
            description: "Sasuke collections",
            img: process.env.PUBLIC_URL + '/images/banners/banner1.png'
        },
        {
            name: "Anime Character Collection 2022",
            description: "Tanjiro collections",
            img: process.env.PUBLIC_URL + '/images/banners/banner2.png'
        },
        {
            name: "Anime Character Collection 2022",
            description: "Luffy collections",
            img: process.env.PUBLIC_URL + '/images/banners/banner3.png'
        }
    ]
    return (<Carousel autoPlay={true} showThumbs={false} showStatus={false}  >
        {
            carouselItems.map(ci =>
                <Box className="accueilBanner" key={ci.description}>
                    <Box className="abText">
                        <h3>{ci.name}</h3>
                        <h1>{ci.description}</h1>
                        <Button className="semiRounded" sx={{ mx: "auto" }} >Achetez maintenant</Button>
                    </Box>
                    <img src={ci.img} alt="Bannière de l'accueil" />
                    <section></section>
                </Box>)
        }
    </Carousel>)
}

export const ProductTypeSelledCards = () => {
    let ptscItems = [
        { name: 'Homme', year: "2020", img: "URL(" + process.env.PUBLIC_URL + "/images/banners/homme.png)" },
        { name: 'Femme', year: "2020", img: "URL(" + process.env.PUBLIC_URL + "/images/banners/femme.png)" },
        { name: 'Accessoire', year: "2021", img: "URL(" + process.env.PUBLIC_URL + "/images/banners/accessoire.png)" }]
    return (<Box className="productTypeCards">
        {
            ptscItems.map(ptscItems =>
                <Card className="ptscCard" sx={{
                    backgroundImage: ptscItems.img, my: 5
                }} key={ptscItems.name}>
                    <CardActionArea >
                        <CardContent sx={{ mt: "20px" }} >
                            <Typography gutterBottom variant="h4" fontWeight="bold" >
                                {ptscItems.name}
                            </Typography>
                            <Typography variant="body2" color="text.secondary" fontWeight="bold">
                                {ptscItems.year}
                            </Typography>
                        </CardContent>
                    </CardActionArea>
                    <CardActions className="ptscCardBtn">
                        <Button size="small" color="primary">
                            Voir
                        </Button>
                        <hr></hr>
                    </CardActions>
                </Card>)
        }</Box>
    );


}


export const ProductOverview = ({ props }) => {
    const location = useLocation();
    const tempData = props.data;
    const [typeProduct, setTypeProduct] = useState('Tout');
    const [finalData, setFinalData] = useState(props.data);
    const intoCart = props.intoCart;

    const viewProductDetail = (item, setItem) => {
        setItem(item);
        document.querySelector('.productModal').classList.toggle('pmVisible')
    }

    const handleChange = (event, newValue) => {
        setTypeProduct(newValue);
        let copyData = tempData.slice();
        const newData = copyData.filter(fl => newValue != 'Tout' ? fl.category === newValue : fl.id > 0);
        setFinalData(newData);
    };

    const handleSearch = (event) => {
        let keyword = event.target.value.toLowerCase();
        let searchRes = tempData.filter(data => data.title.toLowerCase().includes(keyword));
        setFinalData(searchRes)
    }

    const showFilterAndSearchBtn = () => {
        if (location.pathname == '/Store') {
            return (<>
                <Box className="filterOrSearchBtns">
                    <Button variant="outlined"> Filtrer </Button>
                    <Button variant="outlined" sx={{ ml: 2 }}
                        onClick={() => toggleClass('searchOnStorePart')}>Chercher</Button>
                </Box>
            </>)
        }
    }

    const showSelectBtnResult = () => {
        return <div className="searchOnStorePart">
            <input type="text" onChange={(event) => handleSearch(event)} />
        </div>
    }
    return (
        <Box className="productPreview">
            <Tabs
                value={typeProduct}
                onChange={handleChange}
                variant="scrollable"
                scrollButtons={false}
                aria-label="scrollable prevent tabs example"
                sx={{ my: 2 }}
            >
                <Tab label="Tout les produits" value="Tout" />
                <Tab label="Homme" value="Homme" />
                <Tab label="Femme" value="Femme" />
                <Tab label="Bijoux" value="jewelery" />
                <Tab label="Electronique" value="electronics" />
            </Tabs>
            {showFilterAndSearchBtn()}
            {showSelectBtnResult()}
            <Box sx={{
                width: "100%", mt: 4, display: "flex", flexWrap: 'wrap',
                justifyContent: 'space-between'
            }}
                className="po">
                {
                    finalData.map(pdt =>
                        <Box component="div" key={pdt.id} className="productCard">
                            <Box>
                                <img src={process.env.PUBLIC_URL + '/images/' + pdt.category + '/' + pdt.image} />
                            </Box>
                            <Box>
                                <Typography variant="body1">
                                    <span>{pdt.title}</span> <br />
                                    <b>€ {pdt.price}</b>
                                </Typography>
                                <Box>
                                    <AddShoppingCart
                                        className={intoCart != null && intoCart.filter(fl => fl.id == pdt.id).length > 0
                                            ? 'addedToCart' : ''}
                                        onClick={(event) => {
                                            event.target.classList.toggle('addedToCart')
                                            props.addToCart(pdt)
                                        }} />
                                    <FavoriteBorderOutlined />
                                </Box>
                            </Box>
                            <Button className='semiRounded' onClick={() => viewProductDetail(pdt, props.setPP)}>
                                Voir Plus</Button>
                        </Box>)
                }
            </Box>
        </Box>
    )
}

































































